<template>
  <div class="about-bg text-center" id="about">
    <div class="spacer">
      <b-container>
        <h1>About</h1>
        <hr class="transparent">
        <b-row>
          <b-col lg="6">
            <div class="about-text text-left">
              <h3>
                Bio
              </h3>
              <p>
                HTML5, CSS3, Jquery Media query         
                 Block Chain Marketing(ICO)  , Vue.js   /      .
              </p>
              <p>
                 ,     .
                , , ,         .<br>
                           
                          .
              </p>
              <p>
                           .
              </p>
              <dl>
                <dt></dt>
                <dd>
                  <span :key="index" v-for="(environment,index) in environmentList">
                    <b-badge variant="primary">#{{ environment.title }}</b-badge>&nbsp;
                  </span>
                </dd>
              </dl>
            </div>
          </b-col>
          <b-col lg="6">
            <div class="text-left">
              <h3>Skills</h3>
              <VueApexCharts type="radar" :options="chartOptions" :series="series"></VueApexCharts>
            </div>
            
          </b-col>
        </b-row>
      </b-container>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import VueApexCharts from 'vue-apexcharts'
Vue.use(VueApexCharts)

export default {
  components: {
    VueApexCharts
  },
  data() {
    return {
      environmentList: [
        {
          title: 'mac',
        },
        {
          title: 'ubuntu',
        },
        {
          title: 'aws',
        },
        {
          title: 'node',
        },
        {
          title: 'npm',
        },
        {
          title: 'php',
        },
        {
          title: 'mysql',
        },
      ],
      series: [{
          name: 'Series 1',
          data: [4, 3, 3, 3, 5],
      }],
      chartOptions: {
        labels: ['HTML/CSS', 'VueJS', 'jQuery', 'Javascript', 'Bootstrap'],
        title: {
          show: false,/*
          text: 'Skills',
          style: {  
            fontSize:  '1.75rem',
            color:  '#ffffff'
          },*/
        },
        chart: {
          width: '100%',
          toolbar: {
            show: false
          }
        },
        markers: {
          size: 0,
        },
        tooltip: {
           enabled: false,
          
        },
        dataLabels: {
          style: {
            fontSize: '14px',
            fontFamily: 'Helvetica, Arial, sans-serif',
            colors: ['#FF5733', '#41B883', '#7ACEF4', '#F0DB4F', '#ad8fda']
          }
        },
        plotOptions: {
          radar: {
            size: 140,
            offsetX: 0,
            offsetY: 0,
          }
        },
        responsive: [{
          breakpoint: 700,
          options: {
            plotOptions: {
              radar: {
                size: 80,
              }
            }
          }
        }]
      }
      
    }
  }
}
</script>

<style>
  .about-bg {
    background-color: #333748;
    color: #fff;
  }
</style>



